/*----container css----*/
.container {
	max-width: 100%;
	height: 100%;
}
.divcontainer {
	max-width: 90%;
	margin-left: auto;
	margin-right: auto;
}	
.subcontainer {
	max-width: 80%;
	margin-left: auto;
	margin-right: auto;
}


.d-inline{
	display: inline-block;
}
@media screen and (max-width: 550px){
	
	.container {
		max-width: 100%;
		height: 100%;
	}
	.divcontainer {
		max-width: 95%;
		margin: 0px auto;
	}	
	.subcontainer {
		max-width: 90%;
		margin: 0 auto;
	}
}
@media screen and (min-width: 767px){
	.container {
		max-width: 100%;
		height: 100%;
	}
	.divcontainer {
		max-width: 95%;
		margin: 0px auto;
	}	
	.subcontainer {
		max-width: 90%;
		margin: 0 auto;
	}
	.row {
		display: flex;
		flex-wrap: wrap;
		max-width: 100%;
		padding: 0px;
		box-sizing: border-box;
	}
	.col-2 {
		flex: 0 0 20%;
		max-width: 20%;
	}
	.col-3 {
		flex: 0 0 30%;
		width: 30%;
	}
	.col-4 {
		flex: 0 0 40%;
		width: 40%;
	}
	.col-5 {
		flex: 0 0 50%;
		width: 50%;
	}
	.col-6 {
		flex: 0 0 60%;
		width: 60%;
	}
	.col-8 {
		flex: 0 0 80%;
		width: 80%;
	}
	.col-el-3{
		flex: 0 0 25%;
		width: 25%;
	}
	.col-el-4{
		flex: 0 0 33.333%;
		width: 33.333%;
	}
	.col-el-6{
		flex: 0 0 50%;
		width: 50%;
	}
	.col-el-8{
		flex: 0 0 66.666%;
		width: 66.666%;
	}
	.col-el-9{
		flex: 0 0 75%;
		width: 75%;
	}
}